// OFF CANVAS SLIDING MENU
// Based on code by Diego Eis
// --------------------------------------------------

@use "sass:math";

.sliding-menu-button-article {
	top: $gutter;
	right: $gutter;
	width: $button-size * 1.5;
	height: $button-size * 1.5;
	background-color:rgba(0, 0, 0, 0.0);
	outline: 0;
	padding: 0;
	border: 2.5px solid transparent;
	cursor: pointer;
	z-index: 5;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	@include media($medium) {
		@include transform(0);
	}
	@include transition(right 600ms cubic-bezier(.645,.045,.355,1.000));
	&.slide {
		@include media($medium) {
			right: 90%;
		}
		@include transition(right 600ms ease-in-out);
	}
}


.sliding-menu-button {
	top: $gutter * 1.3;
	right: $gutter;
	display: block;
	width: $button-size * 1.5;
	height: $button-size * 1.5;
	background-color:rgba(0, 0, 0, 0.0);
	outline: 0;
	padding: 0;
	padding-top: 6px;
	border: 2.5px solid transparent;
	cursor: pointer;
	z-index: 5;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	@include media($medium) {
		@include transform(0);
	}
	@include transition(right 500ms cubic-bezier(.645,.045,.355,1.000));
	&.slide {
		@include media($medium) {
			right: 90%;
		}
		@include transition(right 500ms ease-in-out);
	}
}

.sliding-menu-content {
	position: fixed;
	top: 0;
	right: 0;
	padding-top: 0.5em;
	text-align: left;
	visibility: hidden;
	z-index: 1000;
	width: 33.333%;
	height: 100%;
	max-width: 400px;
	min-width: 280px;
	@include media($small) {
		padding: em(20) em(20);
		width: 50%;
	}
	@include media($micro) {
		padding: em(20) em(15);
		width: 80%;
	}
	@include transform(translateX(100%));
	@include transition(500ms cubic-bezier(.645,.045,.355,1.000));
	background: $sliding-menu-background;
	overflow-y: auto;
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
	&.is-visible {
		visibility: visible;
		@include transform(translateX(0));
		@include transition(500ms ease-in-out);
	}
	ul {
		margin: 0 0 0 2em;
		padding: 0;
	}
	ul,
	li {
		list-style: none;
	}
	li {
		display: block;
		position: relative;
		padding: 1em 0;
	}
	.menu-item > li {
		a {
			color: $white;
			text-decoration: none;
		}
		.teaser {
			width: 150px;
			border: 2px solid $white;
			margin-bottom: ($gutter * 0.5);
			@include media($medium) {
				position: absolute;
				top: 20px;
				left: 0;
				margin-bottom: 0;
			}
		}
		.title {
			display: block;
			font-family: $alt-font;
			@include font-size(32,no);
			font-weight: 700;
			@include media($medium) {
				margin-left: 170px;
			}
		}
		.excerpt {
			color: $white;
			margin-top: 0;
			@include media($medium) {
				margin-left: 170px;
			}
		}
	}
	.sub-menu-item > li a {
		display: block;
		color: $white;
		font-style: italic;
	}
	.menu-item .home a {
		@include font-size(32);
	}
}

.menu-screen {
	@include position(fixed, 0px 0px 0px 0px);
	@include transition;
	background: $sliding-menu-border-color;
	opacity: 0;
	visibility: hidden;
	z-index: 4;
	&.is-visible {
		opacity: .4;
		visibility: visible;
		&:hover {
			cursor: pointer;
		}
	}
}

// animated navicons
// https://github.com/SaraSoueidan/navicon-transformicons
// common to all three-liners
@mixin menuline {
	display: inline-block;
	width: $button-size;
	height: math.div($button-size, 6);
	border-radius: math.div($button-size, 14);
	transition: $transition;
	background-color: inherit;
}
// animated navicons
// https://github.com/SaraSoueidan/navicon-transformicons
// common to all three-liners
@mixin menuline-white {
	background-color: white;
}

@mixin menuline-black {
	background-color: black;
}

// common to all three-liners
.menulines {
	display: inline-block;
	width: $button-size;
	height: math.div($button-size, 6);
	border-radius: math.div($button-size, 14);
	transition: $transition;
	&.black {
		@include menuline-black;
	}
	&.white {
		@include menuline-white;
	}

	position: relative;
	&:before, &:after {
		@include menuline;
	  	position: absolute;
		left:0;
		content: '';
		//-webkit-transform-origin: $button-size/14 center;
		//transform-origin: $button-size/14 center;
	}
	&:before {
		top: $button-size*0.25;
	}
	&:after {
		top: -$button-size*0.25;
	}
}
// for both the arrow up and left icons
.menulines-button.arrow.close {
		.menulines {
			&:before,
			&:after {
				top: 0;
				width: math.div($button-size, 1.8);
			}
			&:before {
				-webkit-transform: rotate3d(0,0,1,40deg);
								transform: rotate3d(0,0,1,40deg);
			}
			&:after {
				-webkit-transform: rotate3d(0,0,1,-40deg);
								transform: rotate3d(0,0,1,-40deg);
			}
		}
}
// arrow up only: just rotate by 90degrees
.menulines-button.arrow-up.close {
	-webkit-transform: scale3d(.8,.8,.8) rotate3d(0,0,1,90deg);
					transform: scale3d(.8,.8,.8) rotate3d(0, 0, 1,90deg);
}
// three-lines to minus only
.menulines-button.minus.close{
	.lines {
		&:before, &:after{
			-webkit-transform: none;
							transform: none;
			top:0;
			width: $button-size;
		}
	}
}
// three-lines to x
.menulines-button.x.close{
	.menulines {
		background: transparent;
		&:before, &:after{
			-webkit-transform-origin: 50% 50%;
							transform-origin: 50% 50%;
			top:0;
			width: $button-size;
		}
		&:before{
			-webkit-transform: rotate3d(0,0,1,45deg);
							transform: rotate3d(0,0,1,45deg);
		}
		&:after{
			-webkit-transform: rotate3d(0,0,1,-45deg);
							transform: rotate3d(0,0,1,-45deg);
		}
	}
}
// three-lines to x method 2
.menulines-button.x2{
		.menulines{
			//transition: background .3s .5s ease;
			&:before, &:after{
				//set transform origin
			-webkit-transform-origin: 50% 50%;
							transform-origin: 50% 50%;
				transition: top .3s .6s ease, -webkit-transform .3s ease;
				transition: top .3s .6s ease, transform .3s ease;
			}
		}
}
.menulines-button.x2.close{
	.menulines {
		transition: background .3s 0s ease;
		background: transparent;
		&:before, &:after{
			transition: top .3s ease, -webkit-transform .3s .5s ease;//delay the formation of the x till the minus is formed
			transition: top .3s ease, transform .3s .5s ease;//delay the formation of the x till the minus is formed
			top:0;
			width: $button-size;
			background-color: white;
		}
		&:before{
			-webkit-transform: rotate3d(0,0,1,45deg);
							transform: rotate3d(0,0,1,45deg);
		}
		&:after{
			-webkit-transform: rotate3d(0,0,1,-45deg);
							transform: rotate3d(0,0,1,-45deg);
		}
	}
}

