// COMPONENTS
// --------------------------------------------------

// BADGES
// --------------------------------------------------

.badge {
	$badge-background: $primary-color;
	$badge-dark-color: $black;
	$badge-danger-color: $danger-color;
	$badge-info-color: $info-color;
	$badge-warning-color: $warning-color;
	$badge-success-color: $success-color;
	$badge-font-color: #fff;

	display: inline-block;
	background: $badge-background;
	border-radius: 2em;
	color: $badge-font-color;
	font-family: $alt-font;
	@include font-size(12,no);
	font-weight: 600;
	line-height: 1;
	padding: .25em 1em;
	text-align: center;

	&.inverse {
		background: $white;
		color: $text-color;
	}

	&.info {
		background: $badge-info-color;
		color: $white;
	}

	&.danger {
		background: $badge-danger-color;
		color: $white;
	}

	&.warning {
		background: $badge-warning-color;
		color: darken($badge-warning-color, 60);
	}

	&.success {
		background: $badge-success-color;
		color: darken($badge-success-color, 60);
	}
}

// NOTICES
// --------------------------------------------------

@mixin notice($notice-color) {
	position: relative;
	padding: 1.5em;
	font-family: $alt-font;
	@include font-size(14,39);
	color: $white;
	background-color: $notice-color;
	border-radius: $border-radius;
	a {
		color: $white;
		border-bottom: 1px dotted $white;
	}
}
.notice {
	@include notice($primary-color);
}
.notice-inverse {
	@include notice($white);
	color: $text-color;
	a {
		color: $text-color;
	}
}
.notice-info {
	@include notice($info-color);
}
.notice-warning {
	@include notice($warning-color);
}
.notice-success {
	@include notice($success-color);
}
.notice-danger {
	@include notice($danger-color);
}

// GIST STYLES
// --------------------------------------------------

//Set max height of embedded gist 
.gist div {
	max-height:75em;
}

//Remove extra padding
.gist table {
	margin-top:0px;
}

//Remove hover effect
.gist tbody
{
	tr:hover > td, tr:hover > th {
		background-color:transparent;
	};
	background-color:white; //Background color white
}
