@mixin post-title {
      @include font-size(22,no, 26);
      color: #333333;
      font-family: $base-font;
      text-transform: uppercase;
      color: #333333;
      margin-top: 0.5em;
      margin-bottom: 0em;
      a {
        text-decoration: none;
      }
}

@mixin post-category {
      text-transform: uppercase;
      color: #919191;
      @include font-size(11);
      a a:hover, a:visited, a:link, a:active {
        color: #919191;
        text-decoration: none !important;
        border-bottom: none !important;
      }
}

@mixin post-excerpt {
      @include font-size(15);
      margin-top: 0.5em;
      margin-bottom: 0em;
      color: #333333;
}

@mixin post-meta {

  .entry-date-icon {
    display:inline-block;
    margin: auto 0px auto auto;
    width: 11px;
    height: 11px;
    text-align: center;
  }
  .entry-date {
    display:inline-block;
    @include font-size(11);
    color: #808386;
    margin-right: 10px;
    margin-bottom: 0px;
  }
  .comment-count-icon {
    display:inline-block;
    margin: auto 0px auto auto;
    width: 11px;
    height: 11px;
  }
  .comment-count {
    display:inline-block;
    @include font-size(11);
    color: #808386;
    margin: auto 0px auto auto;
  }
  .comment-like-icon {
    display:inline-block;
    margin: auto 0px auto auto;
    width: 11px;
    height: 11px;
  }
  .comment-like {
    display:inline-block;
    @include font-size(11);
    color: #808386;
    margin: auto 0px auto auto;
  }
}

@mixin inline-btn-small {
  .inline-btn { 
    a { div { span { display: none; } } }
    i {
      width: 10px;
      &.fa-facebook { &:before { margin-left: 1px; } }
    }
  }
}

@mixin hover-except-mobile {
    border: 1px solid transparent;

    &:hover {
      border: 1px solid #919191;
    }

    @include media($micro) {
      border: none;
      &:hover { border: none; }
    }  
    @include media($small) {
      border: none;
      &:hover { border: none; }
    }

}

article{
    font-family: "Open Sans";
    padding-left: 0.5em;
    padding-right: 0.5em;
    padding-top: 0.5em;
    @include hover-except-mobile;
}

.tile {

    margin-bottom: $gutter;
    margin-left: 2px; //font for char gets cut on animations. For instance "J"

    .line {
      margin-top: 3%;
      width: 15%;
        border-bottom: solid #808386;
        border-bottom-width: 2px;
    }
    .post-title {
      @include post-title;
    }

    .post-teaser {
      position: relative;
      display: block;
      &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba($base-color,0);
        pointer-events: none;
        @include transition(background 0.3s);
      }
    }

    .post-category {
      @include post-category;
      margin-top: 0.5em;
      margin-bottom: 0.5em;
    }

    .post-excerpt {
      @include post-excerpt;
      a {
        text-decoration: none;
      }
    }

    .post-meta {
      overflow:auto;
      position:relative;
      @include post-meta;
    }

}

.share-title {
  margin-left: 16px;
}

@mixin adapt-tile {

  .sh-elements {
    font-family: "Open Sans";
    width: 100%;

    .sh-left {
      width: 100%;
      a {
        text-align:center;
      }
    }

    .sh-rigth {
      //clear: left;
      //float: left;
      width: 100%;
      .post-title {
        @include post-title;
      }

      .post-category {
        @include post-category;
        margin-top: 0.5em;
        margin-bottom: 0.5em;
     }

      .post-excerpt {
        @include post-excerpt;
        a {
          text-decoration: none;
        }
      }

      .post-meta {
        overflow:auto;
        position:relative;
        @include post-meta;
      }
      .share-this {
        padding-top: 0.5em;
        @include inline-btn-small;
        @include inline-btn-small;
      }
    }
  }
}

@mixin adapt-share {
  @include outer-container;
  margin-bottom: $gutter;
  padding-bottom: 0.5em;
  .sh-elements {
    @include span-columns(9);
    padding-left: 0.5em;
    padding-bottom: 0.5em;
    padding-top: 0.5em;
    @include hover-except-mobile;

    .sh-left {
      @include span-columns(4);
    }
    .sh-rigth {
      @include span-columns(6);
      &.wide{
        @include span-columns(11);
      }

      .post-title {
        @include post-title;
        text-decoration: none;
      }
      .post-excerpt {
        @include post-excerpt;
        margin-bottom: 0.5em;
        &.wide{
          @include span-columns(11);
        }
        a {
          text-decoration: none;
        }
      }
      .post-meta {
        @include span-columns(11);
        @include post-meta;
        padding-top: 0.5em;
        padding-bottom: 0.5em;
      }
      .post-category {
        @include post-category;
        padding-top: 0.5em;
        padding-bottom: 0.5em;
        margin: 0;
        padding: 0;
        margin-top: -6px; //pushes text to div top hedge
      }
      .share-this {
        @include span-columns(11);
        .inline-btn {
          a {
            .base {
              opacity: 0.3;
            }
            &:hover .base { opacity: 1; }
          }
        }
        @include media($micro) {
          @include inline-btn-small;
        }
        @include media($small) {
          @include inline-btn-small;
        }
      }
    }
  }
}

article {
  // bad implementation here with the sh-elements problem
  border: none;
  &:hover { border:none;}
  &.adapt-tile-to-share {
    @include adapt-share;
    @include media($micro) {
      @include adapt-tile;
    }
    @include media($small) {
      @include adapt-tile;
    }
  }
}
