// GLOBAL
// --------------------------------------------------

@use "sass:math";

.wrap {
  @include outer-container;
}

.come-in {
  @include transform(translateY(20px));
  @include animation(pop 0.5s ease forwards);
}

.already-visible {
  @include transform(translateY(0));
  @include animation(none);
}

.hidden,
.load {
  display: none;
}

.no-scroll {
  overflow: hidden;
}

.inline-btn {
  @include clearfix;
  margin-top: 3em;
  a, btn {
    display: inline-block;
    margin-right: $gutter * 0.5;
    &:last-child {
      margin-right: 0;
    }
  }
}

// shorten length to improve readability
@include media($medium) {
  .shorten {
    width: percentage(math.div(8, 12));
  }
}

.center {
  text-align: center;
}

.image-right {
  display: block;
  margin-left: auto;
  margin-right: auto;
  @include media($medium) {
    float: right;
    margin-left: $gutter;
  }
}


// thumbnail image grid default
.th-grid {
  @include row($direction: LTR);
  @include clearfix;
  margin: 0;
  padding: 0;
  li {
    list-style: none;
    @include span-columns(3);
    @include omega(4n);
    margin-bottom: flex-gutter($grid-columns);
  }
  a {
    img {
      // Image hover animation
      &:hover {
        @include animation(pop .3s 0 linear);
        box-shadow: 0 0 10px rgba($black, .20);
      }
    }
  }
}
// thumbnail image grid oversized
.th-grid-full {
  @include clearfix;
  margin: 0;
  padding: 0;
  @include media($large) {
    margin-right: -29em; // magic number ugh
  }
  @at-root {
    .archive-wrap & {
      margin-right: 0;
    }
  }  
  li {
    list-style: none;
    margin-bottom: flex-gutter($grid-columns);
    @include media($micro) {
      @include span-columns(3);
      @include omega(4n);
    }
    @media screen and (min-width: em(480)) and (max-width: em(999)) {
      @include span-columns(3);
      @include omega(4n);
    }
    @include media($large) {
      float: left;
      width: 6.575em;
      margin-right: .25em; // magic number ugh
      margin-bottom: .25em; // magic number ugh
      @include omega(9n);
    }
  }
  a {
    img {
      // Image hover animation
      &:hover {
        @include animation(pop .3s 0 linear);
        box-shadow: 0 0 10px rgba($black, .20);
      }
    }
  }
}

// ANIMATIONS
// --------------------------------------------------

@include keyframes(wiggle) {
	25%, 50%, 75%, 100% { @include transform-origin(top center); }
	25% { @include transform(rotate(8deg)); }
	50% { @include transform(rotate(-4deg)); }
	75% { @include transform(rotate(2deg)); }
	100% { @include transform(rotate(0deg)); }
}

@include keyframes(pop) {
	50% { @include transform(scale(1.1)); }
	100% { @include transform(scale(1)); }
}

@include keyframes(hang) {
	50% { @include transform(translateY(-3px)); }
	100% { @include transform(translateY(-6px)); }
}

@include keyframes(fadin) {
  0%   { opacity: 0;   }
  25%  { opacity: 0.4; }
  50%  { opacity: 0.6; }
  75%  { opacity: 0.8; }
  100% { opacity: 1; }
}

.hang {
	display: inline-block;
	@include animation-name(hang);
	@include animation-duration(0.5s);
	@include animation-timing-function(linear);
	@include animation-iteration-count(infinite);
	@include animation-direction(alternate);
}

.fadin-anim {
  @include animation-name(fadin);
  @include animation-duration(1.3s);
  @include animation-delay(0.0s);
  @include animation-iteration-count(1);
  @include animation-timing-function(linear);
  @include animation-direction(alternate);
}
